<template>
  <div>
	<div class="container1">
      <div class="row">
        <div class="col-md-12 text-center">
          <card type="secondary"
                  header-classes="bg-white pb-5"
                  body-classes="px-lg-5 py-lg-5"
                  class="border-0 mb-0">
                <template>
                    <div class="text-muted text-center mb-3">
                        <small>If you are not registered and are a part of IITK Community...</small>
                    </div>
                    <div class="btn-wrapper text-center">
                        <base-button type="success">
                            Register
                        </base-button>
                    </div>
                </template>
                <template>
                    <div class="text-center text-muted mb-4">
                        <small>Or sign in with credentials</small>
                    </div>
                    <form role="form">
                        <base-input alternative
                                    class="mb-3"
									v-model="email"
									v-on:keyup.enter="signIn"
                                    placeholder="Registered Email"
									autocomplete="username"
                                    addon-left-icon="ni ni-email-83">
                        </base-input>
                        <base-input alternative
                                    type="password"
									v-model="password"
									v-on:keyup.enter="signIn"
                                    placeholder="Password"
									autocomplete="current-password"
                                    addon-left-icon="ni ni-lock-circle-open">
                        </base-input>
                        <base-checkbox v-model="remember">
                            Remember me
                        </base-checkbox>
                        <div class="text-center">
                            <base-button type="danger" class="my-4" v-on:click="signIn">Sign In</base-button>
                        </div>
                    </form>
                </template>
            </card>
        </div>
      </div>
	</div>
  </div>
</template>
<style>

</style>
<script>
  import firebase from "firebase";
  export default {
    data() {
      return {
		remember: true,
        year: new Date().getFullYear(),
		email: '',
		password: '',
      }
    },
	methods: {
	  signIn: function() {
		if (!this.remember) {
			firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION).then(function() {
				console.log("Remember me not selected");
			}).catch(function(error){
				console.log("Failed to set persistence: " + error.message)
			});
		}
		var _this = this;
		firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
		  function(user) {
			_this.$router.replace('dashboard')
		  },
		  function(err) {
			_this.$notify({type: 'danger', horizontalAlign: 'center', message: 'Invalid Username or Password!'})
		  }
		);
	  }
	},
  };
</script>
